









































































































// @ts-ignore
import vueSlider from 'vue-slider-component';
import {Component, Vue, Watch} from 'vue-property-decorator';
import {Scatter} from '@/modules/eos/scatter';
import {Network} from '@/modules/eos/network';
import {EOS} from '@/modules/eos/eos';
import {Dice, RollMode} from '@/modules/contract/dice/dice';
import {EOSAccountsTable} from '../../modules/eos/accountsTable';
import {Asset} from '../../modules/eos/asset';
import {Token} from '../../modules/eos/token';
import HistoryTableView from '@/views/dice/historyTableView.vue';
import Loading from '@/components/loading.vue';
import {isPresent, toFixed} from '../../modules/utils/commonFunctions';
import {TransactionReceipt} from '../../modules/eos/transactionReceipt';
import {HistoryRow} from '../../modules/contract/dice/HistoryTable';

@Component({
    components: {
        HistoryTableView,
        vueSlider,
        Loading,
    },
})
export default class DiceView extends Vue {
    public static readonly DICE_CONTRACT = 'codemonkeyte';

    public betNumber: number = 49;
    public allBetTicker: any = null;
    public betInfoTicker: any = null;
    public contractTicker: any = null;
    public globalTicker: any = null;
    public balanceTicker: any = null;
    public rollAnimationTicker: any = null;

    public balanceEffectVisible: boolean = false;
    public rollLoading: boolean = false;
    public allTableLoading: boolean = true;

    public balanceEffectStyle: any = {'color': 'green', 'margin-left': '10 px'};
    public rollValue: string = '00';
    public pendingRollValue: number = 0;
    public balanceDelta: number = 0;
    public curBetId: number = 0;
    public activeIndex: number = 0;
    public rollMode: RollMode = RollMode.Under;
    public rollReal: string = '';
    public showedBetIds: any = {};
    public underSlide: any = {
        roll: this.betNumber,
        height: 12,
        dotWidth: 20,
        dotHeight: 24,
        min: 2,
        max: 97,
        speed: 0.1,
        tooltip: 'hover',
        bgStyle: {
            backgroundColor: 'transparent',
            border: '1px solid #938282',
        },
        tooltipStyle: {
            backgroundColor: '#666',
            borderColor: '#666',
        },
        processStyle: {
            backgroundColor: '#0DD985',
            margin: '-1px',
            height: '12px',
        },
    };
    public overSlide: any = {
        roll: this.betNumber,
        height: 12,
        dotWidth: 20,
        dotHeight: 24,
        min: 2,
        max: 97,
        speed: 0.1,
        tooltip: 'hover',
        bgStyle: {
            backgroundColor: '#0DD985',
            marginTop: '0px',
            height: '12px',
        },
        tooltipStyle: {
            backgroundColor: '#666',
            borderColor: '#666',
        },
        processStyle: {
            backgroundColor: '#230304',
            border: '1px solid #938282',
            boxShadow: '0 0 20px #230304',
        },
    };
    public amount: number = 1;
    public balance: Asset = new Asset(0, Token.EOS);
    public betAccountBalance: Asset = new Asset(10000, Token.EOS);
    public valueRoll: number = 50;
    public allData: any[] = [];
    public rollStartTime: number = 0;

    private eos = new EOS(new Scatter(Network.KYLIN));
    private dice = new Dice(DiceView.DICE_CONTRACT,  this.eos);

    public isLoggedIn(): boolean {
        return this.eos.loggedIn();
    }

    public get isRollUnder(): boolean {
        return this.rollMode === RollMode.Under;
    }

    public get cur_odds(): string {
        return ((100 - 2) / this.cur_win_chance).toFixed(2);
    }

    public get cur_profit() {
        return (this.amount * ((100 - 2) / this.cur_win_chance)).toFixed(2);
    }

    public get cur_win_chance(): number {
        return this.getWinChance(this.rollMode, this.betNumber);
    }

    public get rollStyle(): any {
        if (this.rollMode === RollMode.Under) {
            return parseInt(this.rollValue, 10) < this.betNumber ? {color: '#0dd985'} : {color: '#9f0000'};
        } else {
            return parseInt(this.rollValue, 10) > this.betNumber ? {color: '#0dd985'} : {color: '#9f0000'};
        }
    }

    public get top(): string {
        return - this.activeIndex * 45 + 'px';
    }

    public get transition(): string {
        return this.activeIndex === 0 ? 'none' : 'top 0.5s';
    }

    public mounted(): void {
        this.updateContractBalance();
        this.startTicker();
    }

    public beforeDestroy(): void {
        this.stopTicker();
    }

    public handleOver(): void {
      this.rollMode = RollMode.Over;
    }

    public handleUnder(): void {
      this.rollMode = RollMode.Under;
    }

    public getWinChance(rollType: RollMode, betNumber: number): number {
        return rollType === RollMode.Under ? betNumber : 99 - betNumber;
    }

    public getOdds(rollType: RollMode, betNumber: number) {
        return ((100 - 2) / this.getWinChance(rollType, betNumber));
    }

    public stepAmount(times: number): void {
        this.amount = this.amount * times;
    }

    public setMin(): void {
        this.amount = 0.01;
    }

    public setMax(): void {
        this.amount = this.balance.quantity;
    }

    public startTicker(): void {
        const that: DiceView = this;
        this.stopTicker();

        this.allBetTicker = setInterval(() => {
            that.updateAllBetTable();
        }, 5000);

        // this.globalTicker = setInterval(function() {
        //     that.updateGlobalInfo();
        // }, 3000);

        // this.contractTicker = setInterval(() => {
        //     that.updateContractBalance();
        // }, 5000);
    }

    public stopTicker(): void {
        if (this.allBetTicker !== null) {
            clearInterval(this.allBetTicker);
            this.allBetTicker = null;
        }

        if (this.contractTicker !== null) {
            clearInterval(this.contractTicker);
            this.contractTicker = null;
        }

        if (this.globalTicker !== null) {
            clearInterval(this.globalTicker);
            this.globalTicker = null;
        }
    }

    public async updateBalance(): Promise<any> {
        if (this.isLoggedIn()) {
            this.balance = await new EOSAccountsTable(this.eos, this.eos.account.name).getBalance();
        } else {
            this.balance = new Asset(0, Token.EOS);
        }
    }

    public async updateContractBalance(): Promise<any> {
        this.betAccountBalance = await new EOSAccountsTable(this.eos, DiceView.DICE_CONTRACT).getBalance();
    }

    // public updateGlobalInfo(): void {
    // }


    public dragUnderEnd(value: any): void {
    }

    public getTimeStamp(): number {
        const date = new Date();
        return Math.floor(date.getTime() / 1000);
    }

    public async bet(): Promise<any> {
        if (!this.eos.loggedIn) {
            await this.eos.connect();
        }

        if (this.eos.loggedIn) {
            await this.updateBalance();
        }

        this.rollLoading = true;

        try {
            const result: TransactionReceipt = await this.dice
                    .play(this.rollMode, this.betNumber, new Asset(this.amount, Token.EOS));


            this.balanceEffectVisible = true;
            const payAmount: number = -(this.amount);
            this.startBalanceEffect(payAmount, 1000);

            this.balance = this.balance.add(payAmount);
            // that.startRollAnimation();
            this.rollStartTime = this.getTimeStamp();
            this.getRollValueFromTrans(result.id, 1);
        } catch (e) {
            this.rollLoading = false;
        }
    }

    public async updateAllBetTable(): Promise<any> {
        this.allData = await this.dice.historyTable.readAll();
        this.allTableLoading = false;
    }

    public async getRollValueFromTrans(id: string, retry: number): Promise<any> {
        try {
            const trans: any = await this.eos.getTransaction(id);
            const targetValue = trans.block_num;
            if (retry === 1) {
                this.pendingRollValue = targetValue
            } else if (this.pendingRollValue !== targetValue) {
                this.getRollValueFromTrans(id, 1);
                return;
            }

            const endTime = this.getTimeStamp();
            if (retry >= 3 || (endTime - this.rollStartTime) >= 5) {
                const result: HistoryRow | undefined= this.getRollValueFromTable(this.rollStartTime);
                if (isPresent(result)) {
                    this.rollLoading = false;
                    const rollValue = result!.rollNumber;
                    this.rollValue = rollValue < 10 ? '0' + rollValue : rollValue.toString(10);

                    await this.updateBalance();
                    if (result!.profit.quantity > 0) {
                        this.startBalanceEffect(result!.profit.quantity);
                    }
                    return;
                }
            }
            this.getRollValueFromTrans(id, retry + 1);
        } catch (e) {
            this.getRollValueFromTrans(id, retry + 1);
        }
    }

    public getRollValueFromTable(time: number): HistoryRow | undefined {
        const accountName: string = this.eos.account.name;
        for (const bet of this.allData) {
            if (bet.player === accountName && bet.timestamp >= time) {
                return bet;
            }
        }
        return undefined;
    }

    public startBalanceEffect(delta: number, time: number = 4000): void {
        this.stopBalanceEffect();

        if (delta < 0) {
        this.balanceEffectStyle.color = 'red'
        } else {
        this.balanceEffectStyle.color = 'green'
        }

        this.balanceDelta = parseFloat(toFixed(delta, 4));
        this.balanceEffectVisible = true;

        const that: DiceView = this;
        setTimeout(() =>  that.balanceEffectVisible = false, time);
    }

    public stopBalanceEffect(): void {
        if (this.balanceTicker !== null) {
        clearTimeout(this.rollAnimationTicker);
        this.balanceTicker = null;
        }
    }
}


